<template lang="pug">
.video-background
    v-container.default-container(style="margin-top: 0px")
        div
            h2(style="color:white; margin-bottom: 2em; text-align: start") Intro
        .video-wrap
            video.elevation-10.video(ref="ticketeerVideo" :class="{'hide-shadow': constrolsVisible}" :controls="constrolsVisible" @pause="paused")
                source(src="ticketeer.mp4")
            .play-button-wrap 
                .play-bubtton.playBut(@click="play" v-show="!constrolsVisible")
                    
                    svg(version="1.1"
                        xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xmlns:a="http://ns.adobe.com/AdobeSVGViewerExtensions/3.0/"
                        x="0px" y="0px" width="213.7px" height="213.7px" viewBox="0 0 213.7 213.7" enable-background="new 0 0 213.7 213.7"
                        xml:space="preserve")

                        polygon(class='triangle' id="XMLID_18_" fill="none" stroke-width="7" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="10" points="73.5,62.5 148.5,105.8 73.5,149.1 ")
                    
</template>

<script>
    export default {
        name: 'Concepts',

        data: () => ({
            constrolsVisible: false
        }),
        methods: {
            paused: function() {
                this.constrolsVisible = false
            },
            play: function() {
                this.$refs.ticketeerVideo.play()
                this.constrolsVisible = true
            }
        }
    }
</script>

<style lang="stylus" scoped>
.video-background {
  background-color: #8099eb
}

.video-wrap
    position relative
    width 100%
    height auto

.video
    width 100%
    height auto

.hide-shadow
    box-shadow none !important

.play-button-wrap 
    position absolute
    display flex
    top 0
    left 0
    pointer-events none

    width 100%
    height 100%
    justify-content center
    align-items center

    .play-bubtton
        font-size 50px
        cursor pointer
        pointer-events auto


.container {
  width: 100%;
  text-align: center;
  margin-top: 25vh;
}

.circle {
  stroke: #FFFFFF;
  stroke-dasharray: 650;
  stroke-dashoffset: 650;
  -webkit-transition: all 0.5s ease-in-out;
  opacity: 0.3;
}

.playBut {
/*  border: 1px solid red;*/
  display: inline-block;
  -webkit-transition: all 0.5s ease;
  
  .triangle {
    -webkit-transition: all 0.7s ease-in-out;
    stroke-dasharray: 240;
    stroke-dashoffset: 480;
    stroke: #FFFFFF;
    transform: translateY(0) 
   }
  
  
  &:hover {
    
    .triangle {
      stroke-dashoffset: 0;
      opacity: 1;
      stroke: #FFFFFF;
      animation: nudge 0.7s ease-in-out;
      
      @keyframes nudge{
        0% {
          transform: translateX(0)  
        }
        30% {
          transform: translateX(-5px)
        }
        50% {
          transform: translateX(5px)
        }
        70% {
          transform: translateX(-2px)
        }
        100% {
          transform: translateX(0)
        }
      }
    }
    
    .circle {
      stroke-dashoffset: 0;
      opacity: 1;
    }
    
  }
}
</style>
